.App{
    overflow: visible;
    display: flex;
}
.page{
    width: 90%;
    height: 100%;
    position: absolute;
    // background: blue;
    left: 9vw;
    overflow: visible;
}

@media screen and (max-width: 942px) {
    .App{
        display: block;
        overflow:visible;
        // background: green;
    }

    .page {
      display: inline-block;
      position: absolute;
      overflow: visible;
      top: 100px;
    //   background: blue;
      width: 100%;
      left: 0;
      justify-content: center;
    }
  
}
