.text-animate{
    font-family: 'Poppins', sans-serif;
    display: inline-block;
    opacity: 0;
    animation: bounceIn 0.8s 1s;
    animation-fill-mode: forwards;
    min-width: 10px;
}




@for $i from 1 through 35 {
    .text-animate._#{$i} {
        animation-delay: #{$i /10}s;
    }
}



