
.about-page{
    // background: yellow;
    overflow: visible;
    .text-zone{
        // position: absolute;
        left: 5vw;
        top: 10vh;
        // background: yellow;
        width: 59%;
        height: 90%;
        margin-top: 10vh;
        margin-left: 5vw;
        overflow: visible;
        h1{
            font-size: 5vw;
            margin: 0;
            padding: 0;
            animation: textanimate 1s 6s forwards;
            color: #8794cc;
            letter-spacing: 3px;
            position: relative;
        }

        h2{
            font-family: 'Poppins', sans-serif;
            font-size: 1.5vw;
            padding-left: 1vw;
            // line-height: 0;
            letter-spacing: 3px;
            animation: fadeIn 2s 4s  backwards;
        }

        p{
            font-family: 'Poppins', sans-serif;
            font-size: 15px;
            padding: 7vh 0;
            color: #606c9e;
            word-spacing: 1px;
            animation: slideIn 1s ease-in-out 7s forwards; 
            opacity: 0;
        }

        #highlight{
            font-family: 'Poppins', sans-serif;
            animation: animatehighlight 1s ease-in-out 9s forwards;
        }

        .change-color1{
            color: #fff;
            text-shadow:0 0 3px #fff,
                        0 0 75px grey,
                        0 0 90px #fff;
        }
        .change-color2{
            color: #fff;
            text-shadow:0 0 2px #fff,
                            0 0 50px #fff, 
                            0 0 70px grey;
        }
    }   
}

@keyframes textanimate {
    
    100%{
        color: #fff;
        text-shadow:0 0 3px #fff,
                        0 0 75px grey,
                        0 0 90px #fff;
    }
}

@keyframes fadeIn {
    0%{
        color: grey;
        opacity: 0;
    }

    50%{
        color: grey;
        opacity: 0.5;
    }
    
    100%{
        color: #fff;
        text-shadow:0 0 3px #fff,
                        0 0 75px grey,
                        0 0 90px #fff;
    }
}

@keyframes slideIn {
    from{
        transform: translateX(-1000px);
    }
    to{
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes animatehighlight {
    0%{
        opacity: 1;
    }

    100%{
        opacity: 1;
        color: #fff;
        text-shadow:0 0 2px #fff,
                            0 0 50px #fff, 
                            0 0 70px grey;
    }
}

@media screen and (max-width: 942px) {
.about-page{
    position: static;
    width: 100%;
    display: inline;

    .text-zone{
        // position: absolute;
        // background: yellow;
        width: 90%;
        left: 50vw;
        top: 30%;
        text-align: center;
    }
}
    
  
}
