.contact-page{
    // background: yellow;
    height: 100%;
    .text-zone{
        position: absolute;
        left: 5vw;
        top: 10vh;
        // background: yellow;
        width: 85%;
        height: 90%;
        h1{
            font-size: 70px;
            margin: 0;
            padding: 0;
            animation: textanimate 1s 4s forwards;
            color: #8794cc;
            letter-spacing: 3px;
        }

        .contact-section{
            display: flex;
            // background: yellow;
            width: 95%;
        }

        p{
            width: 50%;
            font-family: 'Poppins', sans-serif;
            font-size: 14px;
            padding: 70px 0;
            color: #606c9e;
            word-spacing: 1px;
            // background: red;
            animation: slideIn 1s ease-in-out 3s forwards; 
            opacity: 0;
        }

        #highlight{
            font-family: 'Poppins', sans-serif;
            animation: animatehighlight 1s ease-in-out 5s forwards;
        }
    }

    .contact-form{
        // background: green;
        width: 50%;
        padding: 0 30px;
        padding-top: 70px;
        animation: slideDown 1s ease-in-out 4s forwards;
        opacity: 0;
        ul{
            padding: 0;
            padding-top: 10px;
            margin: 0;
        }
        li{
            padding: 0;
            margin: 0;
            list-style: none;
            margin-bottom: 10px;
            overflow: hidden;
            position: relative;
            display: block;
            clear: both;
        }

        li.identifier{
            width: 45%;
            // margin-right: 5%;
            float: left;
            clear: none;

            &:first-child{
                margin-right: 10%;
            }
        }

    }

    input[type='text'],
    input[type='email']{
        width: 100%;
        border: 0;
        border-bottom: 2px solid white;
        background: #0d1d3100;
        color: #8794cc;
        height: 30px;
        box-sizing: border-box;
        font-family: 'Poppins', sans-serif;
    }

    textarea{
        width: 100%;
        height: 100px;
        background: #0d1d3100;
        color: #8794cc;
        border: 0;
        border-bottom: 2px solid white;
        font-family: 'Poppins', sans-serif;
    }

    .button{
        font-family: 'Poppins', sans-serif;
        letter-spacing: 2px;
        background: #606d9e00;
        border: 1px solid white;
        width: 200px;
        height: 50px;
        color: white;
        border-radius: 2px;
    }

    .button:hover{
        text-shadow:0 0 4px #fff,
                        0 0 75px grey,
                        0 0 90px #fff;
        letter-spacing: 4px;
        transition: all 0.3s ease-in-out;
        cursor: pointer;
    }
}

@keyframes slideDown {
    from{
        transform: translateY(-1000px);
    }
    to{
        transform: translateY(0);
        opacity: 1;
    }
}

@media screen and (max-width: 942px) {
    .contact-page{
        position: static;
        width: 100%;
        display: inline-block;
        padding: 0;
        margin: 0;
    
        .text-zone{
            // position: absolute;
            // background: yellow;
            width: 90%;
            text-align: center;
            padding: 0;
            margin: 0;

            h1{
                font-size: 9vw;
                margin-top: 0vh;
            }

            .contact-section{
                display: inline;
                padding: 0;
                margin: 0;
                p{
                    width: 100%;
                    text-align: center;
                    padding: 0;
                    margin: 0;
                    margin-top: 5vh;
                }

                .contact-form{
                    width: 100%;
                    margin: 10vh 0;
                    padding: 0;
                    display: flex;
                    // background: orange;
                    justify-content: center;
    
                }
            }
        }
    }
        
      
}