.profile-container{
    z-index: 0;
    width: 30%;
    height: 500px;
    position: absolute;
    left: 62vw;
    top: 10%;
    // background: red;
    border-radius: 50%;
    width: 195px;
    height: 200px;
    background-image: url(moon3.jpg);
    animation: slide 1s ease-in-out 8s forwards; 
    opacity: 0;
}

.profile-picture{
    width: 195px;
    height: 200px;
    box-sizing: border-box;
    border-radius: 50%;
    // margin-left: 100px;

}

.profile-container:hover{
    box-shadow: 0px 0px 40px grey,
                0px 0px 60px grey;
    scale: 1.1;
    transition: 1s ease-in-out;
}

.profile-picture:hover{
    transition: 0.8s ease-in-out;
    opacity: 0;
}

.profile-picture.clicked{
    opacity: 0;
}

.profile-container.clicked{
    box-shadow: 0px 0px 40px grey,
    0px 0px 60px grey;
scale: 1.1;
}

@keyframes slide {
    from{
        transform: translatey(-300%);
    }
    to{
        transform: translateY(0);
        opacity: 1;
    }
    
}

@media screen and (max-width: 942px) {
    .profile-holder{
        display: flex;
        // background: #ffa500;
        justify-content: center;
        padding-top: 20px;
    }
    .profile-container{
        position: static;
        z-index: 1;
        width: 30%;
        height: 500px;
        top: 10%;
        left: 72vw;
        // background: red;
        border-radius: 50%;
        width: 195px;
        height: 200px;
        background-image: url(moon3.jpg);
        animation: slide 1s ease-in-out 8s forwards; 
        opacity: 0;
    }
  
}