* {
    font-family: sans-serif;
}

.side-bar{
    // background: #181818;
    display: block;
    background:#000411;
    min-height: 100vh;
    width: 120px;
    top: 0;
    z-index: 3;
    position: fixed;
    bottom: 0;
    overflow: visible;


    .logo{
        text-align: center;
        // background: yellow;
        height: 15vh;
    }

    #logo {
        width: 100px;
        height: auto;
    }

    nav {
        width: 100%;
        display: block;
        text-align: center;
        padding: 10px 0;
        min-height: 75vh;
        // background:#000411;
        // position:absolute;
        // background: blue;

        a{
            display: block;
            font-size: 13px;
            margin: 50% 0;
            position: relative;
            height: 51px;
            line-height: 51px;
            text-decoration: none;
            color: #434b6d;
            font-family: Poppins, sans-serif;
            letter-spacing: 1px;
    
            &:hover{
                transition: all 0.3s ease-in-out;
                color: #fff;
                text-shadow:0 0 5px #fff,
                            0 0 40px #fff;
                letter-spacing: 3px;
            }
        }

        a.active{
            color: #fff;
            text-shadow:0 0 5px #fff,
                        0 0 40px #fff;
        }
    }

   
}

#socials {
    display: flex;
    list-style: none;
    width: 120px;
    text-align: left;
    margin: 0;
    padding: 0;
    padding-top: 2vh;
    justify-content: center;
    // background: yellow;
    // background:#000411;

    li {
        font-size: 20px;
        padding: 0 5px;
        &:hover{
            svg{
                color: #fff;
                transition: all 0.3s ease-in-out;
                filter: drop-shadow(0px 0px 10px rgb(225 225 225)); 
            }
        }
    }    

}

@media screen and (max-width: 942px) {
    .side-bar{
        // background: #181818;
        display: flex;
        background:#000411;
        min-height: 100px;
        max-height: 100px;
        width: 100%;
        top: 0;
        z-index: 3;
        position: fixed;
        overflow: visible;
        align-items: center;
        // background: red;
    
        .logo{
            text-align: center;
            padding-left: 2vw;
            // background: yellow;
            height: 100%;
        }
    
        #logo {
            width: 100px;
            height: auto;
        }
    
        nav {
            width: 100%;
            height: 100%;
            display: flex;
            text-align: center;
            padding:0 20px;
            // background:#000411;
            // position:absolute;
            // background: blue;
            justify-content: space-between;
            justify-self: center;
            align-items: center;
    
            a{
                font-size: 1.7vw;
                margin: 0;
                height: 51px;
                text-decoration: none;
                color: #434b6d;
                font-family: Poppins, sans-serif;
                letter-spacing: 1px;
                // background: #fff;
            }
        }
    }


    #socials {
        display: inline;
        list-style: none;
        width: 100px;
        height: 70%;
        text-align: center;
        margin: 0;
        padding: 0;
        justify-content: center;
        // background: yellow;
        // background:#000411;
    
        li {
            font-size: 16px;
            padding: 0;
            padding-top: 6.5%;
            &:hover{
                svg{
                    color: #fff;
                    transition: all 0.3s ease-in-out;
                    filter: drop-shadow(0px 0px 10px rgb(225 225 225)); 
                }
            }
        } 
    }   
}