.experience-page{
    // background: yellow;
    height: auto;
    .text-zone{
        // position: absolute;
        left: 5vw;
        top: 10vh;
        margin: 0;
        margin-left: 5vw;
        margin-top: 10vh;
        // background: yellow;
        width: 85%;
        height: 90%;
        h1{
            font-size: 70px;
            margin: 0;
            padding: 0;
            animation: textanimate 1s 6s forwards;
            color: #8794cc;
            letter-spacing: 3px;
        }

        h2{
            font-family: 'Poppins', sans-serif;
            font-size: 20px;
            padding-left: 10px;
            // line-height: 0;
            letter-spacing: 3px;
            animation: slidefromside 1s ease-in-out 4s forwards;
            opacity: 0;
        }
        #highlight{
            animation: animatehighlight 1s ease-in-out 7s forwards;
        }
        p{
            font-family: 'Poppins', sans-serif;
            font-size: 18px;
            padding: 70px 0;
            color: #606c9e;
            word-spacing: 1px;
            animation: slidefromside 1s ease-in-out 5s forwards; 
            opacity: 0;
        }
        #download{
            text-decoration: none;
            font-family: 'Poppins', sans-serif;
            color: #fff;
        }

        #download:hover{
            text-shadow:0 0 3px #fff,
                        0 0 75px grey,
                        0 0 90px #fff;
            transition: all 0.3s ease-in-out;
            letter-spacing: 2px;
        }
    }
}

@keyframes textanimate {
    
    100%{
        color: #fff;
        text-shadow:0 0 3px #fff,
                        0 0 75px grey,
                        0 0 90px #fff;
    }
}

@keyframes slidefromside {
    from {
        transform: translateX(-1000px);
        color: #8794cc;
    }
    to{
        opacity: 1;
        transform: translateX(0);
        color: #8794cc;
    }
}

@keyframes slideUp {
    from{
        transform: translateY(1000px);
    }
    to{
        transform: translateY(0px);
        opacity: 1;
    }
    
}

@media screen and (max-width: 942px) {
    .experience-page{
        position: static;
        width: 100%;
        display: inline;
    
        .text-zone{
            // position: absolute;
            // background: yellow;
            width: 100%;
            text-align: center;
            padding: 0;
            margin: 0;

            h1{
                font-size: 9vw;
                margin-top: 10vh;
            }
        }
    }
        
      
}